.profile {
  max-width: 300px;
  margin: 0 auto;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  border: 2px solid blue;
}

.description {
  padding: 30px;
  justify-content: center;
  text-align: center;
}

.avatar {
  display: block;
  width: 60%;
  height: auto;
  margin: 0 auto;

  border-radius: 50%;
  background-color: red;
}

.name {
  margin-bottom: 0;
  font-size: 24px;
  font-weight: 700;

  color: blue;
}

.tag {
  margin-top: 10px;
  margin-bottom: 0;
  color: red;
}

.location {
  margin-top: 10px;
  margin-bottom: 0;
  color: red;
}

.stats {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: blue;
}

.stats li {
  padding: 20px 15px;
  border-top: 2px solid blue;
  width: calc(100% / 3);
}

.stats li:not(:last-child) {
  border-right: 2px solid white;
}

.label {
  display: block;
  margin-bottom: 5px;

  font-size: 12px;
  font-weight: 500;
  color: red;
}

.quantity {
  display: block;

  font-weight: 700;
  color: white;
}
